  .sandwichButton {
    display: inline-block;
    cursor: pointer;
    padding-left: 30px;
  }
  
  .bar1, .bar2, .bar3 {
    width: 20px;
    height: 3px;
    background-color: white;
    margin: 6px 0;
    transition: 0.4s;
  }
  
  /* Rotate first bar */
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
    transform: rotate(-45deg) translate(-9px, 6px) ;
    margin-left:30px;
  }
  
  /* Fade out the second bar */
  .change .bar2 {
    opacity: 0;
    margin-left:30px;
  }
  
  /* Rotate last bar */
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
    transform: rotate(45deg) translate(-8px, -8px) ;
    margin-left:30px;
  }

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: block;
    margin-left: 10px;
    margin-top: 10px;
    width: 100%;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 250px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
}

li {
    list-style-type: none;
    cursor: pointer;
}

li:hover {
    background-color: rgba(0,0,0,0.1);
    list-style-type: none;
}