.col2 {
    height: 100px;
}

.searchForm {
    width: 100%;
    margin: 0px;
    padding: 0px;
}

.searchAll {
    left: 1%;
    right: 1%;
    width: 97%;
    position: sticky;
    margin: 5px;
    border: 1px solid rgba(30, 85, 127, 0.3);
    border-radius: 3px;
    font-size: 15px;
    color: rgba(30, 85, 127, 0.3);
    text-indent: 10px;
}

.searchAll:focus{
    border-color: #497799;
    color: black;
    flex: 1;
}

.currentChat {
    text-align: left;
    border-left: 1px solid rgba(30, 85, 127, 0.3);
    border-right: 1px solid rgba(30, 85, 127, 0.3);
    width: 100%;
}

.colLeft {
    border-left: 1px solid rgba(30, 85, 127, 0.3);
    text-align: left;
}

.chats {
    padding-left: 30%;
    padding-top: 10px;
    height: 50px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 300px;
}

.blue  {
    background: rgba(30, 85, 127, 0.3);
}

.list-group {
    list-style: none;
}

@media only screen and (min-width: 992px) {
    .columns {
        height: calc(100vh - 50px);
    }
}

@media only screen and (max-width: 992px) {
    .columns {
        height: calc(88vh - 50px);
    }
}

.newChat {
    position: sticky;
    margin-left: -5px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid rgba(30, 85, 127, 0.3);
    border-radius: 3px;
    font-size: 15px;
    color: rgba(30, 85, 127, 0.3);
    text-indent: 10px;
    width: 72%;
}

.newChat:focus{
    border-color: #497799;
    color: black;
}

.addNewChat{
    position: relatve;
    bottom: 20px;
    right: 203px;
    border: none;
    background-color: transparent;
    color: #497799;
    font-weight: bold;
    outline: none !important;
}

.autofocus {
    border-width: medium;
}

.exitSearchBtn {
    height: 20px;
    margin-top: 2%;
    margin-right: 30px;
    position: absolute;
    right: -15px;
    border: none;
    background-color: Transparent;
    outline:none;
    color: rgba(30, 85, 127, 0.3)
}

.exitSearchBtn:focus {
    border: none;
    outline:none;
}